.instructions {
  &__header {
    h1 {
      font-size: 18pt;
      font-weight: bold;
      font-family: 'PT Sans', sans-serif;
      margin-bottom: 10px;
      padding: 7px;
      border-bottom: 1px solid #c9c9c9;
      color: $gunmetal;
      letter-spacing: 1px;
    }
  }

  &__content {
    padding: 7px;
    font-family: 'Open Sans', sans-serif;
    font-size: 14pt;
    color: $medium-gray;
  }

  &__actions {
    h2 {
      padding: 7px 7px 0;
      font-family: 'Open Sans', sans-serif;
      font-size: 14pt;
      color: $medium-gray;
    }

    &__button {
      margin-top: 7px;
    }
  }
}
