.modal {
  &__header {
    &__title {
      display: block;
      border-bottom: 2px solid $light-gray;
      font-weight: bold;
      font-size: 14pt;
      color: $gunmetal;
      padding: 5px 0 15px;
      font-family: 'Open Sans', sans-serif;
      margin-bottom: 10px;
    }
  }
  &__body {
    font-family: 'Open Sans', sans-serif;
    font-size: 14pt;
    color: $medium-gray;
    &__text {
      margin: 5px 0;
      display: block;
    }
  }
}
