@import '../config/variables';

.nav {
  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    background: $white;
    border-bottom: 2px solid $light-gray;
    padding: 15px 0;
  }

  &__item {
    font-family: 'PT Sans', sans-serif;
    font-size: 15pt;
    font-weight: bold;
    letter-spacing: 1px;

    &:hover {
      text-decoration: none;
      color: $primary-color-lighter !important;
    }

    &:focus {
      text-decoration: none;
      color: $primary-color-lighter !important;
    }
  }
}

.no-margin {
  margin: 0 !important;
  padding: 4px 10px !important;
}
