@import '../config/variables';

.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 40px;

  .detail {
    &__header {
      padding: 15px;

      &__title {
        font-family: 'Open Sans', sans-serif;
        display: flex;
        font-size: 20pt;
        font-weight: bold;
        align-content: center;
        color: $gunmetal;
      }

      &__date {
        font-family: 'Open Sans', sans-serif;
        font-size: 19pt;
        font-weight: bold;
        display: flex;
        justify-content: flex-end;
        align-content: center;
        color: $gunmetal;
      }
    }

    &__body {
      &__description {
        font-family: 'Open Sans', sans-serif;
        font-size: 16pt;
        padding: 15px;
        color: $medium-gray;
      }

      &__image {
        display: flex;
        justify-content: center;
        align-content: center;
        img {
          width: 55%;
        }
        margin: 20px;
      }
    }
  }

  .detail__button {
    &__wrapper {
      display: flex;
      justify-content: center;
      align-content: center;
    }
  }
}
