.form {
  &__field {
    padding: 5px 7px;

    &__label {
      width: 100%;

      &__text {
        display: block;
        color: $gunmetal;
        font-family: 'Open Sans', sans-serif;
        font-size: 13pt;
        font-weight: bold;
      }
    }

    &__helper {
      margin: 2px 0 5px;
      display: block;
      color: $medium-gray;
      font-family: 'Open Sans', sans-serif;
      font-size: 12pt;
    }

    &__input {
      width: 100%;
      height: 35p;
      font-size: 14pt;
      padding: 10px 5px;
      font-family: 'Open Sans', sans-serif;
      letter-spacing: 1px;
      color: $gunmetal;
      border-radius: 3px;

      &--textarea {
        height: 105px;
      }
    }

    &__radio {
      margin: 5px 0;
      &-group {
        // padding: 5px;
      }

      &__text {
        margin-left: 10px;
        color: $gunmetal;
        font-family: 'Open Sans', sans-serif;
        font-size: 12pt;
      }
    }
  }

  &__header {
    padding: 7px;
    border-bottom: 1px solid #c9c9c9;

    &__title {
      h1 {
        font-size: 18pt;
        font-weight: bold;
        font-family: 'PT Sans', sans-serif;
        margin-bottom: 10px;
        color: $gunmetal;
        letter-spacing: 1px;
      }
    }

    &__description {
      font-size: 15pt;
      font-family: 'Open Sans', sans-serif;
      color: $medium-gray;
    }
  }

  &__body {
    padding: 7px 0 0;

    &__field {
      margin: 15px 0;

      &__label {
        display: block;
        font-size: 14pt;
        font-family: 'Open Sans', sans-serif;
        margin-bottom: 20px;
        color: $medium-gray;
      }

      &__input {
        margin: 0 20px;
      }
    }
  }

  &__bottom {
    // padding: 10px 20px 20px;
  }

  &__button {
    &__next {
      min-width: 150px;
      font-family: 'Open Sans', sans-serif;
      border: none;
      font-size: 12pt;
      border-radius: 7px;
      margin: 15px 0;
      padding: 7px 10px;
      cursor: pointer;
      color: $white;
      background: $primary-color;
      letter-spacing: 1px;
      text-transform: uppercase;

      &:hover,
      &:focus {
        transition: ease-in 0.2s;
        background: $primary-color-lighter;
      }
    }
  }
}

.btn {
  &--green {
    min-width: 150px;
    font-family: 'Open Sans', sans-serif;
    border: none;
    font-size: 12pt;
    border-radius: 7px;
    margin: 10px 7px;
    padding: 7px 10px;
    cursor: pointer;
    color: $white;
    background: $green;
    letter-spacing: 1px;
    text-transform: uppercase;

    &:hover,
    &:focus {
      transition: ease-in 0.2s;
      background: $green-lighter;
    }
  }

  &--disabled {
    min-width: 150px;
    cursor: default;
    opacity: 50%;
    letter-spacing: 1px;
    text-transform: uppercase;

    &:hover,
    &:focus {
      background: $green;
    }
  }
}
