.footer {
  margin-top: 50px;

  &__wrapper {
    height: auto;
    padding: 7px;
    background: $white;
    border-top: 1px solid $light-gray;
  }

  &__speech {
    font-size: 16pt;
    font-family: 'Open Sans', sans-serif;
    color: $medium-gray;
  }

  svg {
    width: 25px;
    height: 25px;
  }
}

.icon {
  &__wrapper {
    &--on {
      background: $green;
      border-radius: 35px;
      padding: 10px;
      transition: ease-in 0.2s;
    }
    &--off {
      background: $secondary-color;
      border-radius: 35px;
      padding: 10px;
      transition: ease-in 0.2s;
    }
  }
  &__clickable {
    cursor: pointer;
    &:hover {
      color: $primary-color;
    }
  }
}
