.content {
  &__wrapper {
    padding: 20px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
  }

  &__single {
    display: inline-block;

    &__title {
      padding: 5px;
      font-size: 14pt;
      font-weight: bold;
      font-family: 'Open Sans', sans-serif;
      color: $gunmetal;
    }

    &__description {
      padding: 5px;
      font-size: 12pt;
      font-family: 'Open Sans', sans-serif;
      color: $medium-gray;
    }
  }
}
