.resume {
  &__header {
    padding: 20px;
    border-bottom: 1px solid #c9c9c9;

    &__title {
      h1 {
        font-size: 18pt;
        font-weight: bold;
        font-family: 'PT Sans', sans-serif;
        margin-bottom: 10px;
        letter-spacing: 1px;
      }
    }

    &__description {
      font-size: 14pt;
      font-family: 'Open Sans', sans-serif;
    }
  }

  &__body {
    padding: 20px;

    &__title {
      h2 {
        font-size: 14pt;
        font-weight: bold;
        font-family: 'Open Sans', sans-serif;
      }

      &__underline {
        margin: 5px 0;
        border-bottom: 3px solid #c9c9c9;
      }
    }

    &__content {
      padding: 0;
    }
  }

  &__outter {
    padding: 30px;
  }

  &__next {
    margin: 30px 0;
  }

  &__empty {
    padding: 10px 0;
    font-size: 12pt;
    color: $medium-gray;
    font-family: 'Open Sans', sans-serif;
  }
}

.btn {
  &__white {
    min-width: 150px;
    font-family: 'Open Sans', sans-serif;
    font-size: 12pt;
    border-radius: 7px;
    margin: 15px 0;
    padding: 7px 10px;
    cursor: pointer;
    color: $primary-color;
    background: $white;
    border: 1px solid $primary-color;
    letter-spacing: 1px;
    text-transform: uppercase;

    &:hover,
    &:focus {
      color: $white;
      transition: ease-in 0.2s;
      background: $primary-color;
    }
  }

  &__red {
    min-width: 150px;
    font-family: 'Open Sans', sans-serif;
    font-size: 12pt;
    border-radius: 7px;
    margin: 7px 0;
    padding: 7px 10px;
    cursor: pointer;
    color: $white;
    background: $secondary-color;
    border: none;
    letter-spacing: 1px;
    text-transform: uppercase;

    &:hover,
    &:focus {
      transition: ease-in 0.2s;
      background: $secondary-color-lighter;
    }
  }
}

.dashed {
  &--right {
    border-right: 1px dashed #c9c9c9;
  }
}
