@import '../config/variables';

canvas {
  position: absolute;
  top: 0px;
  left: 20px;
}

.camera {
  &__wrapper {
    padding: 20px;
  }

  &__expression {
    font-weight: bold;
    margin-bottom: 15px;
    font-family: 'Open Sans', sans-serif;
    font-size: 17pt;
    color: $medium-gray;
  }
}

.login {
  &__wrapper {
    //
  }

  &__outter {
    padding: 20px;
  }

  &__header {
    padding: 20px;
    font-family: 'Open Sans', sans-serif;
    font-size: 14pt;
    font-weight: bold;
    color: $gunmetal;
    border-bottom: 1px solid $light-gray;
    margin-bottom: 20px;
  }
}
